.list {
  color: black;
  width: 100%;
  /* background: linear-gradient(#beceba, #e0e4de); */
  background-color: white;
  /* height: 50px; */
  /* margin: 20px auto; */
  border-radius: 0;
}
.list p {
  padding: 10px;
  position: relative;
}

.list p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}
faicons {
  margin: 0 5px;
  font-size: 15px;
}
.list p input {
  background-color: transparent;
  border: 0;
  color: black;
  font-size: 18px;
  width: 90%;
}
.list p input:focus {
  outline: none;
}
